import { GitHub } from "@mui/icons-material";
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  CardMedia,
  Container,
  Divider,
  Link,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import OpenInNewIcon from "@mui/icons-material/OpenInNew";
import Title from "./Title";

const useStyles = makeStyles({
  halfScreenStyle: {
    flexDirection: "column",
  },
  fullScreenStyle: {},
});

type ProjectType = {
  title: string;
  date: string;
  gifUrl: string;
  description: string;
  githubUrl: string;
  projectUrl: string;
};

//     !
const projects: ProjectType[] = [
  {
    //  
    title: "Suika Game",
    //   
    date: "2000.01 - 2000.05",
    // gif 
    gifUrl:
      "https://user-images.githubusercontent.com/14011726/94132137-7d4fc100-fe7c-11ea-8512-69f90cb65e48.gif",
    //  
    description:
      "    .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .        .    .     .    .     .    .     .    .     .    .     .    .     .    .     .    .     .    .         .    .         .    .         .    .         .    .         .    .         .    .         .    .         .    .         .    .         .    .         .    .         .    .     .    .     .    .     .    .     .    .     .    .     .    .     .    .",
    //   
    githubUrl: "https://www.naver.com",
    //     or  
    projectUrl: "",
  },
  {
    title: "Suika2",
    date: "2000.01 - 2000.05",
    gifUrl:
      "https://user-images.githubusercontent.com/14011726/94132137-7d4fc100-fe7c-11ea-8512-69f90cb65e48.gif",
    description:
      "    .    .     .    .",
    githubUrl: "",
    projectUrl: "",
  },
  {
    title: "Suika3",
    date: "2000.01 - 2000.05",
    gifUrl:
      "https://user-images.githubusercontent.com/14011726/94132137-7d4fc100-fe7c-11ea-8512-69f90cb65e48.gif",
    description:
      "    .    .     .    .",
    githubUrl: "",
    projectUrl: "",
  },
];

function Projects() {
  const classes = useStyles();
  const isHalfScreen = useMediaQuery("(max-width:900px)");
  const isTextBreak = useMediaQuery("(max-width:385px)");
  return (
    <Box py="1.5rem" mb="5rem" id="projects" bgcolor="#ebf5ff">
      <Container>
        <Title title="Projects" mb="1.5rem" />
        {projects.map(
          ({ title, date, gifUrl, description, githubUrl, projectUrl }) => {
            return (
              <Card
                /* 
               elevation    shadow  !
                
              */

                elevation={5}
                key={title}
                sx={{ marginBottom: "2rem", position: "relative" }}
              >
                <CardHeader
                  sx={{ textAlign: "center" }}
                  title={title}
                  subheader={date}
                />
                <Box
                  className={
                    isHalfScreen
                      ? classes.halfScreenStyle
                      : classes.fullScreenStyle
                  }
                  display="flex"
                  justifyContent="center"
                  alignItems="center"
                >
                  <CardMedia
                    component="img"
                    //  gif  
                    width="500px"
                    height="500px"
                    image={gifUrl}
                    alt="project-gif"
                    sx={{
                      maxWidth: "500px",
                      padding: "1.2rem",
                      borderRadius: "5%",
                    }}
                  />
                  <CardContent
                    sx={{
                      overflow: "auto",
                      scrollY: "auto",
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "space-between",
                      // gif    
                      height: "500px",
                    }}
                  >
                    <Typography
                      sx={{
                        overflow: "auto",
                      }}
                      mb="1.2rem"
                      variant="body1"
                    >
                      {description}
                    </Typography>
                    <Divider sx={{ marginBottom: "1rem" }} />
                    {/*    */}
                    <Box>
                      <Link
                        mr="1em"
                        target="_blank"
                        rel="noreferrer"
                        href={githubUrl}
                      >
                        <Button
                          color="info"
                          variant="outlined"
                          sx={{ marginBottom: isTextBreak ? "0.5rem" : "0" }}
                          startIcon={
                            <GitHub
                              fontSize="large"
                              style={{ color: "black" }}
                            />
                          }
                        >
                           
                        </Button>
                      </Link>
                      <Link target="_blank" rel="noreferrer" href={projectUrl}>
                        <Button
                          // sx={{ marginBottom: isHalfScreen ? "0.5rem" : "0" }}
                          variant="outlined"
                          startIcon={
                            <OpenInNewIcon
                              fontSize="large"
                              style={{ color: "black" }}
                            />
                          }
                        >
                            
                        </Button>
                      </Link>
                    </Box>
                    {/*    */}
                  </CardContent>
                </Box>
              </Card>
            );
          }
        )}
      </Container>
    </Box>
  );
}

export default Projects;
